  currentTemp: number;
  heroes: any[] = [{"id":1, "name": "anju" , "tel": 122334},
                    {"id":2, "name": "awani" , "tel": 322334},
                    {"id":3, "name": "madhavi" , "tel": 422334}
                  ];
  
  personInfo: any[];

  constructor(private httpClnt: HttpClient){

  }

